//import * as actionTypes from './actionTypes;
import axios from 'axios';


//Création des fonctions créator
export const authStart = () => {
    return {
        type: 'AUTH_START'
    };
};

export const authSuccess = (token, userId) => {
    return {
        type: 'AUTH_SUCCESS',
        idToken: token,
        userId: userId 
    };
};

export const authFail = (error) => {
    return {
        type: 'AUTH_FAIL',
        error: error
    };
};


//Envoie de la fonction de deconnexion
export const logout = () => {
    //Suppression des données dans le local storage
    localStorage.removeItem('token');
    localStorage.removeItem('expirationDate');
    localStorage.removeItem('localId')
    return {
        type: 'AUTH_LOGOUT'
    };
};

//Vérifiaction du temps de connexion pour le checkout et evnoie de la fonction de deconnexion
export const checkAuthTimeout = (expirationTime) => {
    return dispatch => {
        setTimeout(() => {
            dispatch(logout())
        }, expirationTime * 1000)
    };
};

export const auth = (email, password, isSignUp) => {
    return dispatch => {
        //Mise en place du spinner d'attent
        dispatch(authStart());
        const authData = {
            email: email,
            password: password,
            returnSecureToken: true
        }

        //Vérification si l'utilisateur est connecté
        let url = 'https://www.googleapis.com/identitytoolkit/v3/relyingparty/signupNewUser?key=AIzaSyDRXL-iI84DmIofhBfYXxaLKM4POItRLlo'
        if (!isSignUp) {
            url = 'https://www.googleapis.com/identitytoolkit/v3/relyingparty/verifyPassword?key=AIzaSyDRXL-iI84DmIofhBfYXxaLKM4POItRLlo'
        }

        axios.post(url, authData)
        //Gestion de l'authentification réussie
        .then(response => {
            //Stockage des éléments dans le local storage pour la continuité de l'expérience client si rafraichissement de la page
            //Calcul de la date de péremption de l'utilisateur à partir de la date actuelle reformatée par rapport au critères de firebase
            const expirationDate = new Date(new Date().getTime() + response.data.expiresIn * 1000)
            localStorage.setItem('token', response.data.idToken);
            localStorage.setItem('expirationDate', expirationDate);
            localStorage.setItem('localId', response.data.localId);
            //Lancement des fonctions dans l"appli lors de la récupération des données
            dispatch(authSuccess(response.data.idToken, response.data.localId));
            dispatch(checkAuthTimeout(response.data.expiresIn))
        })
        //Gestion de l'erreur
        .catch(err => {
            dispatch(authFail(err.response.data.error));
        })
    }
};

export const setAuthRedirectPath = (path) => {
    return {
        type : 'SET_AUTH_REDIRECT_PATH',
        path: path
    }
}

export const authCheckState = () => {
    return dispatch => {
        const token = localStorage.getItem('token');
        if (!token) {
            dispatch(logout());
        } else {
            const expirationDate = new Date(localStorage.getItem('expirationDate'));
            if (expirationDate < new Date()) {
                dispatch(logout())
            } else {
                const localId = localStorage.getItem('localId')
                dispatch(authSuccess(token, localId));
                dispatch(checkAuthTimeout((expirationDate.getTime() - new Date().getTime())/1000))
            }
        }
    }
}


